// https://vuex.vuejs.org/en/actions.html
import sha256 from 'sha256'
import axios from 'axios'
import qs from 'qs'
import boyTeacher from '@/assets/img/boy_teacher.jpg'
import girlTeacher from '@/assets/img/girl_teacher.jpg'
import boyStudent from '@/assets/img/boy_student.jpg'
import girlStudent from '@/assets/img/girl_student.jpg'
// The login action passes vuex commit helper that we will use to trigger mutations.
export default {
  login ({ commit }, userData) {
    return new Promise((resolve, reject) => {
      localStorage.removeItem('token')
      commit('auth_request')
      let body = {
        userName: userData.username,
        userPwd: sha256(userData.password).toUpperCase()
      }
      axios.post('/api/account/signin/', qs.stringify(body)).then(response => response.data)
        .then(response => {
          if (response.status === 'success') {
            const token = response.data.user_token
            axios.defaults.headers.common['Authorization'] = 'Bearer ' + token
            axios.defaults.paramsSerializer = function (params) { return qs.stringify(params, { token: token }) }
            console.log('APItoken')
            console.log(token)
            console.log('---')
            // axios.defaults.params = {}
            // axios.defaults.params['token'] = token
            if (response.status === 'success') {
              const user = response.data
              if (user.picture === '' || user.picture === null) {
                if (user.type === 'teacher') {
                  if (user.is_male) {
                    user.picture = boyTeacher
                  } else {
                    user.picture = girlTeacher
                  }
                } else if (user.type === 'student') {
                  if (user.is_male) {
                    user.picture = boyStudent
                  } else {
                    user.picture = girlStudent
                  }
                }
              }
              // if (user.type === 'teacher') {
              //   user.picture = 'https://i0.wp.com/c1.staticflickr.com/1/845/43297974751_b369a85da6_z.jpg?resize=498%2C640&ssl=1'
              // } else {
              //   user.picture = 'https://3.share.photo.xuite.net/peirchi/136ed80/9653911/423047145_m.jpg'
              // }
              localStorage.setItem('token', token)
              localStorage.setItem('user', JSON.stringify(user))
              commit('auth_success', { token, user })
              console.log('token')
              console.log(localStorage.getItem('token'))
              console.log('User')
              console.log(JSON.parse(localStorage.getItem('user')))
              // resolve(response)
            }
          }
          resolve(response)
        })
    })
  },
  logout ({ commit }) {
    return new Promise((resolve, reject) => {
      const id = JSON.parse(localStorage.getItem('user')) ? JSON.parse(localStorage.getItem('user')).user_id : ''
      commit('logout')
      let body = {
        userId: id
      }
      axios.post('/api/account/logout/', qs.stringify(body))
      // .then(response => {
      // }
      localStorage.removeItem('token')
      localStorage.removeItem('user')
      console.log(localStorage.getItem('token'))
      console.log(JSON.parse(localStorage.getItem('user')))
      delete axios.defaults.headers.common['Authorization']
      resolve()
    })
  }
}
