    ...mapState(['appName', 'dologinView', 'doResetPwdView']),
    // ...mapGetters(['authorized']),
    // ...mapState('app', ['theme', 'shrink', 'demo']),
    // path () {
    //   return this.$route.matched[0].meta.name
    // },
    // title () {
    //   return this.$route.meta.name
    // },
    userInfo () {
      return JSON.parse(localStorage.getItem('user'))
    }
    // loginStatus () {
    //   if (JSON.parse(localStorage.getItem('user'))) {
    //     return true
    //   } else {
    //     return false
    //   }
    // }
  },

  watch: {
    '$route.fullPath': function (newValue) {
      this.responsive = false
      const { q } = this.$route.query
      this.topSearch = q
    },
    dologinView (val) {
      this.loginView = val
    },
    loginView (val) {
      this.$store.state.dologinView = val
    },
    doResetPwdView (val) {
      this.resetPwdView = val
    },
    resetPwdView (val) {
      this.$store.state.doResetPwdView = val
    }
    // $route (val) {
    // this.path = val.matched[0].meta.name
    // this.title = val.meta.name
    // console.log(val)
    // }
  },

  mounted () {
    if (localStorage.getItem('token') !== null && JSON.parse(localStorage.getItem('user')) !== null) {
      this.loginStatus = true
      this.userInfo = JSON.parse(localStorage.getItem('user'))
    }
    const { q } = this.$route.query
    this.topSearch = q
    // this.onResponsiveInverted()
    // window.addEventListener('resize', this.onResponsiveInverted)
    // this.timer = setInterval(() => {
    //   this.$store.state.app.theme = localStorage.getItem('theme')
    // }, 1000)
  },
  // beforeDestroy () {
  //   window.removeEventListener('resize', this.onResponsiveInverted)
  // },

  methods: {
    test () {
      alert('')
    },
    userEmailFailMsg () {
      if (this.loginError && this.userEmail === '') {
        return ' (Email)'
      } else if (this.userEmail === '') {
        this.userEmailVerify = true
        // return '(Email)'
        return ''
      } else {
        this.userEmailVerify = false
        // return ''
        return ''
      }
    },
    userPwdFailMsg () {
      if (this.loginError && this.userPwd === '') {
        return ' '
      } else if (this.userPwd === '') {
        this.userPwdVerify = true
        // return ''
        return ''
      } else {
        this.userPwdVerify = false
        // return ''
        return ''
      }

    },
    userBirthFailMsg () {
      let isBirth = /^[0-9]{4}-[0-9]{2}-[0-9]{2}$/
      if (this.userBirth !== '' && !isBirth.test(this.userBirth)) {
        this.userBirthVerify = true
        return ' 1900-01-01'
      } else if (this.userBirth !== '') {
        this.userBirthVerify = false
        return ''
      } else if (this.verifyError && this.userBirth === '') {
        this.userBirthVerify = true
        return ' 1900-01-01'
      } else {
        this.userBirthVerify = true
        return '1900-01-01'
      }
    },
    userMobileFailMsg () {
      let isMobile = /^09[0-9]{2}-[0-9]{6}$/
      if (this.verifyError && this.userMobile === '') {
        this.userMobileVerify = true
        return ' 09XX-XXXXXX'
      } else if (this.userMobile === '') {
        this.userMobileVerify = true
        return '09XX-XXXXXX'
      } else if (!isMobile.test(this.userMobile)) {
        this.userMobileVerify = true
        return ' 09XX-XXXXXX'
      } else {
        this.userMobileVerify = false
        return ''
      }
    },
    userResetPwdFailMsg () {
      if (this.resetError && this.userResetPwd === '') {
        this.regResetPwdVerify = true
        return ' '
      } else if (this.userResetPwd === '') {
        this.userResetPwdVerify = true
        return ''
      } else if (this.userResetPwd.length < 8) {
        this.regResetPwdVerify = true
        return ' 8'
      } else {
        this.userResetPwdVerify = false
        return ''
      }
    },
    userResetConfirmPwdFailMsg () {
      if (this.resetError && this.userResetConfirmPwd === '') {
        this.userResetConfirmPwdVerify = true
        return ' '
      } else if (this.userResetConfirmPwd === '') {
        this.userResetConfirmPwdVerify = true
        return ''
      } else if (this.userResetConfirmPwd.length < 8) {
        this.userResetConfirmPwdVerify = true
        return ' 8'
      } else if (this.userResetPwd !== this.userResetConfirmPwd) {
        this.userResetConfirmPwdVerify = true
        return ' '
      } else {
        this.userResetConfirmPwdVerify = false
        return ''
      }
    },
    userOldPwdFailMsg () {
      if (this.modifyError && this.userOldPwd === '') {
        this.userOldPwdVerify = true
        return ' '
      } else if (this.userOldPwd === '') {
        this.userOldPwdVerify = true
        return ''
      } else {
        this.userOldPwdVerify = false
        return ''
      }
    },
    userNewPwdFailMsg () {
      if (this.modifyError && this.userNewPwd === '') {
        this.userNewPwdVerify = true
        return ' '
      } else if (this.userNewPwd === '') {
        this.userNewPwdVerify = true
        return ''
      } else if (this.userNewPwd.length < 8) {
        this.userNewPwdVerify = true
        return ' 8'
      } else {
        this.userNewPwdVerify = false
        return ''
      }
    },
    userNewConfirmPwdFailMsg () {
      if (this.modifyError && this.userNewConfirmPwd === '') {
        this.userNewConfirmPwdVerify = true
        return ' '
      } else if (this.userNewConfirmPwd === '') {
        this.userNewConfirmPwdVerify = true
        return ''
      } else if (this.userNewConfirmPwd.length < 8) {
        this.userNewConfirmPwdVerify = true
        return ' 8'
      } else if (this.userNewPwd !== this.userNewConfirmPwd) {
        this.userNewConfirmPwdVerify = true
        return ' '
      } else {
        this.userNewConfirmPwdVerify = false
        return ''
      }
    },
    loginT () {
      this.userEmail = '111111@1111.com'
      this.userPwd = '        '
      this.login()
    },
    loginS () {
      this.userEmail = '11111@1111.com'
      this.userPwd = '        '
      this.login()
    },
    login () {
      let username = this.userEmail
      let password = this.userPwd
      if (username && password) {
        this.$store.dispatch('login', { username, password })
          .then(response => {
            if (response.status === 'success') {
              this.loginStatus = true
              this.userInfo = JSON.parse(localStorage.getItem('user'))
              this.loginView = false
              this.snackbar = true
              this.color = 'green'
              this.errorMessages = ''
              // this.$router.push('/')
              this.$router.go(0)
            } else {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = response.errMsg
            }
          })
      } else {
        this.loginError = true
      }
    },
    logout () {
      this.$store.dispatch('logout').then(() => {
        this.loginView = false
        this.snackbar = true
        this.color = 'green'
        this.errorMessages = ''
        this.loginStatus = false
        // if (this.$router.history.current.path !== '/') {
        //   this.$router.go(0)
        // }
        this.$router.go(0)
      })
    },
    identity () {
      // 
      if (this.userEmail !== '' && this.userBirthFailMsg().indexOf('')!==-1 && this.userMobileFailMsg().indexOf('')!==-1 ) {
        let body = {
          userName: this.userEmail,
          userBirth: this.userBirth,
          userMobile: this.userMobile
        }
        this.$http.post('/api/lesson/getLessonCardsForCommonUsers/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            if (res.status === 'success') {
              this.snackbar = true
              this.color = 'green'
              this.errorMessages = ''
              this.userID = res.data.userID
              const token = res.data.token
              axios.defaults.headers.common['Authorization'] = 'Bearer ' + token
              axios.defaults.paramsSerializer = function (params) { return qs.stringify(params, { token: token }) }
              this.forgetPwdView = false
              this.resetForgetPwdView = true
            } else {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            }
          })
      } else {
        this.verifyError = true
      }
    },
    doForgetResetPwd () {
      // 
      if (this.userResetPwdFailMsg().indexOf('')!==-1 && this.userResetConfirmPwdFailMsg().indexOf('')!==-1 ) {
        let body = {
          userID: this.userInfo ? this.userInfo.user_id : -1,
          newUserPwd: this.userResetPwd
        }
        this.$http.post('/api/lesson/getLessonCardsForCommonUsers/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            if (res.status === 'success') {
              this.snackbar = true
              this.color = 'green'
              this.errorMessages = ''
              this.resetForgetPwdView = false
              this.loginView = true
            } else {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            }
          })
      } else {
        this.resetError = true
      }
    },
    doResetPwd () {
      // 
      if (this.userOldPwdFailMsg().indexOf('')!==-1 && this.userNewPwdFailMsg().indexOf('')!==-1 && this.userNewConfirmPwdFailMsg().indexOf('')!==-1 ) {
        let body = {
          userID: this.userInfo ? this.userInfo.user_id : -1,
          oldUserPwd: this.userOldPwd,
          newUserPwd: this.userNewPwd
        }
        this.$http.post('/api/lesson/getLessonCardsForCommonUsers/', qs.stringify(body)).then(res => res.data)
          .then(res => {
            if (res.status === 'success') {
              this.snackbar = true
              this.color = 'green'
              this.errorMessages = ''
              this.$store.state.doResetPwdView = false
              this.resetPwdView = false
            } else {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            }
          })
      } else {
        this.modifyError = true
      }
    }
  }
}
</script>
